//##
//右侧固定工具栏
.fixed-tool {
    position: fixed;
    top: 210px;
    right: 50px;
    z-index: 110;
    border: 1px solid #393939;
    border-radius: 10px;

}

.tool-item {
    width: 60px;
    height: 60px;
    line-height: 1.8;
    padding-top: 8px;
    text-align: center;
    background: #464646;
    opacity: .8;
    color: #ffffff;
    border-top: 1px solid #575757;
    border-bottom: 1px solid #393939;
    position: relative;
    .call-me {
        position: absolute;
        top: 0;
        left: 0;
        visibility: hidden;
        background: #323232;
        line-height: 60px;
        white-space: nowrap;
        overflow: hidde;
        width: 60px;
        height: 60px;
        opacity: 0;
        font-size: 20px;
        transition: all .3s ease;
    }
    .qrcode {
        position: absolute;
        top: 0;
        left: 0px;
        width: 60px;
        height: 85px;
        visibility: hidden;
        opacity: 0;
        transition: all .3s ease;
        background: url(../images/qr.jpg) no-repeat;
    }
    &:hover {
        color: #eb9605;
        background: #323232;
        cursor: pointer;
        opacity: 1;
        .call-me {
            height: 60px;
            width: 180px;
            visibility: visible;
            opacity: 1;
            left: -180px;
        }
        .qrcode {
            width: 84px;
            height: 85px;
            left: -85px;
            visibility: visible;
            opacity: 1;
        }
        .icon-tool01 {
            background-position: 0 -192px;
        }
        .icon-tool02 {
            background-position: 0 -224px;
        }
        .icon-tool03 {
            background-position: 0 -256px;
        }
        .icon-tool04 {
            background-position: 0 -288px;
        }
        .icon-tool05 {
            background-position: 0 -320px;
        }
        .icon-tool06 {
            background-position: 0 -352px;
        }
    }
    &._first {
        border-radius: 8px 8px 0 0;
    }
    &._last {
        border-radius: 0 0 8px 8px;
    }
}
