.swiper-slide {
    img {
        width: 100%;
        height: 100%;
    }
}

.swiper-item01 {
    position: relative;
    z-index: 19;
    background: #fff;
    height: 623px;
    width: 100%;
    .swiper-slide {
        background-size: cover;
        background-repeat: no-repeat;
    }
    .banner-01 {
        background: url('../images/banner/banner-index-home-01.jpg') 50%;
    }
    .banner-02 {
        background: url('../images/banner/banner-index-home-01.jpg') 50%;
    }
    .banner-03 {
        background: url('../images/banner/banner-index-home-01.jpg') 50%;
    }
    .pagination {
        position: absolute;
        z-index: 20;
        width: 100%;
        text-align: center;
        bottom: 10px;
    }
    .swiper-pagination-switch,
    .swiper-paginations-switch {
        display: inline-block;
        border-radius: 0;
        background: #fff;
        cursor: pointer;
        width: 50px;
        height: 5px;
        margin: 0 10px;
    }
    .swiper-active-switch {
        background: #000;
    }
    .arrow-left,
    .arrow-right {
        position: absolute;
        top: 300px;
        z-index: 3;
    }
    .arrow-left {
        left: 25px;
    }
    .arrow-right {
        right: 25px;
    }
    .arrow {
        &:after {
            border-width: 18px;
        }
        &:before {
            border-width: 20px;
        }
        &._left {
            &:after {
                border-right-color: #dbdbdb;
            }
        }
        &._right {
            &:after {
                border-left-color: #dbdbdb;
            }
        }
    }
}

.swiper-item02 {
    .swiper-wrapper {
        overflow: hidden;
        width: 190px;
        height: 276px;
        .swiper-slide {
            overflow: hidden;
            background-color: #fff;
            border: 1px solid $color-border;
            box-sizing: border-box;
            margin: 0 10px 10px 0;
            width: 82px !important;
            height: 82px !important;
            &._active,
            &:hover {
                border-width: 2px;
            }
        }
    }
}

.swiper-item03 {
    width: auto !important;
    overflow: hidden;
    padding: 10px 25px;
    box-sizing: content-box;
    position: relative;
    .swiper-wrapper {
        overflow: hidden;
        width: 1000px !important;
        height: 63px !important;
        margin-bottom: 5px;
        .swiper-slide {
            width: 63px !important;
            height: 63px !important;
            margin-left: 3px;
            &:first-child {
                margin-left: 10px;
            }
        }
    }
    .swiper-button-prev,
    .swiper-button-next {
        position: absolute;
        left: 0;
        top: 10px;
        width: 32px;
        height: 63px;
        background: #f8f8f8 url('../images/icon/icon-swiper-button.png') no-repeat;
    }
    .swiper-button-prev {
        background-position: 0 0;
    }
    .swiper-button-next {
        left: 238px;
        background-position: -32px 0;
    }
}

.swiper-item04 {
    display: block;
    width: 168px !important;
    height: 650px !important;
    overflow: hidden;
    box-sizing: content-box;
    background: #fff;
    position: relative;
    .swiper-wrapper {
        overflow: hidden;
        width: 90px !important;
        height: 1000px;
        margin: 20px auto;
        .swiper-slide {
            width: 90px !important;
            height: 150px !important;
            margin: 40px 0 0 0;
            text-align: center;
            img {
                display: block;
                margin: 0 auto;
                width: 70px;
                height: 120px;
            }
            p:first-child {
                font-size: 13px;
                color: #202020;
            }
            p:last-child {
                font-size: 12px;
                color: #7d7d87;
            }
        }
    }
    .swiper-button-prev,
    .swiper-button-next {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 60px;
        background-color: #fff !important;
        background-repeat: no-repeat !important;
        background-position: center center !important;
    }
    .swiper-button-prev {
        background: url('../images/icon/icon-swiper-prev.png');
        &:hover {
            background: url('../images/icon/icon-swiper-prev_hover.png');
        }
    }
    .swiper-button-next {
        top: 590px;
        background: url('../images/icon/icon-swiper-next.png');
        &:hover {
            background: url('../images/icon/icon-swiper-next_hover.png');
        }
    }
}

.swiper-item05 {
    width: 918px;
    height: 180px;
    margin: 30px auto 0;
    .swiper-wrapper {
        width: 2000px;
        margin: 0 0 0 50px;
        .swiper-slide {
            width: 110px !important;
            height: 180px !important;
            margin: 0 50px;
            text-align: center;
            img {
                width: 70px;
                height: 120px;
            }
            p:first-child {
                font-size: 15px;
            }
            p:last-child {
                font-size: 14px;
            }
        }
    }
    .swiper-button-prev,
    .swiper-button-next {
        position: absolute;
        left: 0;
        top: 50px;
        width: 16px;
        height: 43px;
        background: url('../images/icon/icon-swiper-arrow.png') no-repeat;
        background-color: transparent;
    }
    .swiper-button-prev {
        background-position: -16px 0;
        &:hover {
            background-position: 0 0;
        }
    }
    .swiper-button-next {
        left: 900px;
        background-position: -48px 0;
        &:hover {
            background-position: -32px 0;
        }
    }
}

.swiper-item06 {
    .arrow-left,
    .arrow-right {
        width: 46px;
        height: 136px;
        background-color: #bbbbbb;
        opacity: .5;
        filter: alpha(opacity=50);
        position: absolute;
        top: 150px;
        z-index: 3;
        .icon-arrow {
            margin-left: 10px;
            margin-top: 40px;
        }
    }
    .arrow-left {
        left: 0px;
    }
    .arrow-right {
        right: 13px;
    }
}

.swiper-item07 {
    position: relative;
    z-index: 19;
    background: #fff;
    width: 720px;
    height: 380px;
    .swiper-slide {
        margin: 0 auto;
        width: 100%;
        img {
            width: 100%;
            height: 100%;
        }
    }
    .swiper-pagination-fraction {
        font-size: 30px !important;
        z-index: 4 !important;
        bottom: 0 !important;
        color: #c2c2c3 !important;
        background: rgba(0, 0, 0, .3);
        .swiper-pagination-current {
            color: #ff9800 !important;
            font-size: 34px !important;
            font-weight: normal;
        }
        .swiper-pagination-total {
            font-size: 24px !important;
            color: #fff !important;
            font-weight: normal;
            position: relative;
            top: 5px;
        }
    }
}