//== set icon.scss
//
//## css for icon 字体大小不会影响icon的垂直居中的位置,注意 icon 父元素必须设置line-height值为图片高
.icon {
    display: inline-block;
    white-space: nowrap;
    letter-spacing: -1em;
    text-indent: -99999em;
    color: transparent;
    vertical-align: top;
    background-repeat: no-repeat;
    &.icon-search {
        width: 16px;
        height: 16px;
        background-image: url('../images/icon/icon-search.png');
    }
    &.icon-template {
        width: 12px;
        height: 11px;
        background-image: url('../images/icon/icon-template.png');
    }
    &.icon-close {
        width: 16px;
        height: 16px;
        background-image: url('../images/icon/icon-close.png');
        &._solid {
            background-position: 0 0;
        }
        &._hollow {
            background-position: 0 -16px;
        }
        &._black {
            background-position: 0 -32px;
        }
        &._white {
            background-position: 0 -48px;
        }
    }
    &.icon-tool01,
    &.icon-tool02,
    &.icon-tool03,
    &.icon-tool04,
    &.icon-tool05,
    &.icon-tool06 {
        width: 22px;
        height: 22px;
        background-image: url('../images/icon/icon-toolbar.png');
    }
    &.icon-tool01 {
        background-position: 0 0;
    }
    &.icon-tool02 {
        background-position: 0 -32px;
    }
    &.icon-tool03 {
        background-position: 0 -64px;
    }
    &.icon-tool04 {
        background-position: 0 -96px;
    }
    &.icon-tool05 {
        background-position: 0 -128px;
    }
    &.icon-tool06 {
        background-position: 0 -160px;
    }
    &.icon-user,
    &.icon-lock {
        position: absolute;
        left: 15px;
        top: 35px;
        width: 23px;
        height: 25px;
    }
    &.icon-user {
        background: url('../images/icon/icon-user.png') no-repeat;
    }
    &.icon-lock {
        background: url('../images/icon/icon-lock.png') no-repeat;
    }
}



/* 箭头 */

.icon-arrow {
    display: inline-block;
    background: url('../images/icon/icon-arrow.png') no-repeat;
    vertical-align: middle;
    width: 6px;
    height: 11px;
    margin-right: 20px;
    &._arrow-grey {
        background-position: 0 -39px;
    }
    &._arrow-red {
        background-position: 0 -20px;
    }
    &._up {
        width: 13px;
        height: 8px;
        background-position: 0 -155px;
        margin-left: 13px;
    }
    &._down {
        width: 13px;
        height: 8px;
        background-position: 0 -139px;
        margin-left: 13px;
    }
    &._up_xs {
        width: 9px;
        height: 5px;
        background-position: 0 -268px;
        margin-left: 6px;
        &._dark {
            background-position: 0 -299px;
        }
    }
    &._down_xs {
        width: 9px;
        height: 5px;
        background-position: 0 -283px;
        margin-left: 6px;
        &._dark {
            background-position: 0 -320px;
        }
    }
    &._left_xs,
    &._right_xs {
        width: 6px;
        height: 9px;
        margin-left: 6px;
    }
    &._left_xs {
        background-position: 0 -344px;
    }
    &._right_xs {
        background-position: 0 -369px;
    }
    &._right {}
    &._xl {
        width: 14px;
        height: 30px;
    }
    &._arrow-white-left {
        background-position: 0 -60px;
    }
    &._arrow-white-right {
        background-position: 0 -100px;
    }
    &.arrow-swiper-left {
        width: 27px;
        height: 53px;

        background-position: 0 -60px;
    }
    &.arrow-swiper-right {
        width: 27px;
        height: 53px;

        background-position: 0 -100px;
    }
    &.arrow-left-pager,
    &.arrow-right-pager {
        width: 8px;
        height: 18px;
    }
    &.arrow-left-pager {
        background-position: 0 -172px;
    }
    &.arrow-right-pager {
        background-position: 0 -200px;
    }
    &.arrow-left-triangle,
    &.arrow-right-triangle {
        width: 6px;
        height: 10px;
    }
    &.arrow-left-triangle {
        background-position: 0 -228px;
        margin-right: 2px;
    }
    &.arrow-right-triangle {
        background-position: 0 -248px;
        margin-left: 2px;
    }
}



/* arrow */

.arrow {
    position: relative;
    text-indent: -99999em;
    font-size: 0;
    margin-right: 50px;
    padding: 10px 0;
    cursor: pointer;
    &:after {
        display: inline-block;
        content: '';
        border: solid transparent;
        border-width: 8px;
        position: absolute;
        left: -2px;
        top: 0;
        z-index: 2;
    }
    &:before {
        display: inline-block;
        content: '';
        border: solid transparent;
        border-width: 10px;
        position: absolute;
        left: -6px;
        top: 0;
        z-index: 1;
    }
    &._left {
        &:after {
            top: 2px;
            border-right-color: #e8cf24;
        }
        &:before {
            border-right-color: #fff;
        }
    }
    &._right {
        &:after {
            top: 2px;
            border-left-color: #e8cf24;
        }
        &:before {
            left: -2px;
            border-left-color: #fff;
        }
    }
    &._up {
        &:after {
            top: -4px;
            left: -4px;
            border-bottom-color: #e8cf24;
        }
        &:before {
            top: -8px;
            border-bottom-color: #fff;
        }
    }
    &._down {
        &:after {
            top: 2px;
            left: -4px;
            border-top-color: #e8cf24;
        }
        &:before {
            top: 2px;
            border-top-color: #fff;
        }
    }
}



/* triangle */

.triangle {
    position: relative;
    text-indent: -99999em;
    font-size: 0;
    margin-right: 20px;
    &:after {
        display: inline-block;
        content: '';
        border: solid transparent;
        border-width: 6px;
        position: absolute;
        left: -2px;
        top: 0px;
        z-index: 2;
    }
    &:before {
        display: inline-block;
        content: '';
        border: solid transparent;
        border-width: 6px;
        position: absolute;
        left: -2px;
        top: 0px;
        z-index: 1;
    }
    &._all {
        &:after {
            border-color: #333;
        }
    }
    &._left {
        &:after {
            top: -2px;
            border-right-color: #333;
        }
        &._gray {
            &:after {
                border-right-color: #ccc;
            }
        }
    }
    &._right {
        &:after {
            top: -2px;
            border-left-color: #333;
        }
        &._gray {
            &:after {
                border-left-color: #ccc;
            }
        }
    }
    &._up {
        &:after {
            top: -6px;
            left: -2px;
            border-bottom-color: #333;
        }
        &._gray {
            &:after {
                border-bottom-color: #ccc;
            }
        }
    }
    &._down {
        &:after {
            top: 0px;
            left: -2px;
            border-top-color: #333;
        }
        &._gray {
            &:after {
                border-top-color: #ccc;
            }
        }
    }
}