    padding-left: 15px;
    color: #333; }

.input-check {
  position: absolute;
  visibility: hidden; }
  .input-check + label {
    cursor: pointer;
    vertical-align: middle;
    display: inline-block;
    width: 20px;
    height: 20px;
    background: url("../images/icon/icon-checkbox.png") no-repeat 0 0; }
  .input-check:checked + label {
    background-position: 0 -20px; }
  .input-check._checkbox + label {
