/*switch*/
.label-switch {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  cursor: pointer;
  width: 70px;
  height: 30px;
  border-radius: 80px; }
  .label-switch .checkbox {
    width: 70px;
    height: 30px;
    line-height: 30px;
    border-radius: 80px;
    font-size: 12px;
    padding-left: 10px;
    color: #ffffff;
    background: #e5e5e5;
    border: none;
    cursor: pointer;
    position: relative;
    z-index: 0;
    transition-duration: 300ms; }
    .label-switch .checkbox:before {
      content: '个人';
      text-align: right;
      position: absolute;
      left: 0;
      top: 0;
      z-index: 1;
      background: #4cd964;
      color: #ffffff;
      width: 70px;
      height: 30px;
      padding-right: 10px;
      border-radius: 80px; }
    .label-switch .checkbox:after {
      content: ' ';
      background: #fff;
      position: absolute;
      z-index: 2;
      top: 1px;
      left: 1px;
      transform: translateX(0px);
      transition-duration: 300ms;
      width: 28px;
      height: 28px;
      border-radius: 50%; }
  .label-switch input[type="checkbox"] {
    display: none; }
    .label-switch input[type="checkbox"]:checked + .checkbox {
      background: #4cd964; }
      .label-switch input[type="checkbox"]:checked + .checkbox:before {
        transform: scale(0); }
      .label-switch input[type="checkbox"]:checked + .checkbox:after {
        transform: translateX(40px); }

