//== set tab.scss
//
//##
$tab-w: 846px;
$tab-menu-wh: 142px 40px;
$tab-menu-color-hover: #e03636;
.tab {
    width: 100%;
    overflow: hidden;
    position: relative;
    z-index: 1;
}

.tab-menu {
    width: 100%;
    height: 40px;
    line-height: 40px;
    margin: 20px auto;
    border-bottom: 1px solid $color-border;
    a {
        float: left;
        width: nth($tab-menu-wh, 1);
        text-align: center;
        font-size: 14px;
        display: inline-block;
        color: #666;
        &:hover,
        &._active {
            color: $tab-menu-color-hover;
            border-bottom: 2px solid $tab-menu-color-hover;
        }
    }
}

.tab-content {
    width: 100%;
    min-height: 100px;
    margin: 20px auto 15px;
    overflow: hidden;
    .tab-item {
        display: inline-block;
        width: 100%;
        position: relative;
        z-index: 1;
        img {
            display: block;
            margin: 0 auto;
        }
    }
}