.content-login {
    background: url('../images/bg-login.jpg');
    height: 644px;
    padding-top: 122px;
    &.login {
        padding-top: 90px;
    }
}

.login-block {
    margin: 0 auto;
    width: 448px;
    height: 396px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    .login-tit {
        background: #b4cb4f;
        height: 60px;
        line-height: 60px;
        color: #fff;
        font-size: 24px;
        text-align: center;
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
        box-sizing: content-box;
    }
    .input-form {
        width: 446px;
        margin: 0 auto;
        height: 335px;
        background: rgba(255, 255, 255, 0.6);
        text-align: center;
        border: 1px solid #b4cb4f;
        border-top: 0;
        box-sizing: content-box;
    }
    .form-label {
        margin: 25px 0 0 0;
    }
    .input-item {
        width: 338px;
        margin: 0;
    }
    .btn {
        width: 338px;
        border-width: 0;
        background: #2eb3f2;
        color: #fff;
        font-size: 18px;
        height: 50px;
        line-height: 50px;
        padding: 0;
    }
}

.login-content {
    width: $w-content;
    margin: 0 auto;
    .title {
        font-size: 30px;
        color: #fff;
    }
    .en-title {
        color: #fff;
        font-size: 18px;
    }
    .tab {
        width: 352px;
    }
    .tab-menu {
        width: 100%;
        height: 40px;
        line-height: 40px;
        margin: 20px auto 0;
        border: 1px solid #000;
        border-bottom: 0;
        a {
            float: left;
            width: 50%;
            text-align: center;
            font-size: 14px;
            display: inline-block;
            color: #fff;
            border: 0;
            background: #323232;
            &:hover,
            &._active {
                background: #000;
                color: #b4cb4f;
            }
        }
    }
    .tab-content {
        background: rgba(255, 255, 255, 0.6);
        margin: 0 auto;
        border: 1px solid #000;
        padding: 30px 0;
        .input-form {
            width: 350px;
            margin: 0 auto;
            text-align: center;
        }
        .input-item {
            width: 270px;
            margin: 25px 0 0 0;
            padding-left: 50px;
        }
        .form-label {
            margin-left: 0;
            margin-top: 0;
            margin-bottom: 0;
        }
        .btn {

            width: 270px;
            border-width: 0;
            background: #2eb3f2;
            color: #fff;
            font-size: 18px;
            height: 50px;
            line-height: 50px;
            padding: 0;
            margin: 35px 0;
        }
    }
    .login-img {
        margin-top: 125px;
    }
}