//== set search.scss
//
//搜索
.search-area {
    border: 1px solid $color-border;
    width: 230px;
    height: 30px;
    line-height: 28px;
    color: #000;
    box-sizing: content-box;
    .search-in {
        width: 200px;
        padding: 0 5px;
        height: 28px;
        line-height: 28px;
        border-right: 1px solid $color-border;
    }
    .icon-search{
        margin: 8px 0 0 6px;
    }
}

.search-box {
    width: 440px;
    height: 28px;
    border: 2px solid #ff7701;
    display: inline-block;
    box-sizing: content-box;
    ul {
        border: 1px solid #facba2;
        background-color: #fff;
        width: 86px;
        position: absolute;
        top: 33px;
        left: -2px;
        z-index: 10;
        display: none;
        li {
            text-align: center;
            color: #7d7d7d;
            width: 84px;
            height: 24px;
            line-height: 24px;
        }
    }
    h1 {
        width: 74px;
        padding-right: 10px;
        font: lighter 12px '\5b8b\4f53';
        color: #7d7d7d;
        line-height: 24px;
        text-align: center;
        position: relative;
    }
    .search-scope {
        float: left;
        width: 84px;
        height: 24px;
        margin: 2px 0px;
        border-right: 1px solid #b3b3b3;
        position: relative;
    }
    .search-type {
        float: left;
        width: 84px;
        height: 24px;
        margin: 2px 0px;
        border-right: 1px solid #b3b3b3;
        position: relative;
    }
    .search-keyword {
        float: left;
        width: 192px;
        padding-left: 30px;
        height: 28px;
        line-height: 28px;
        border: none;
        background: url('../images/icon/icon-search.png') 7px 6px no-repeat;
        color: #808080;
    }
    .search-btn {
        float: left;
        margin: 0;
        width: 80px;
        height: 28px;
        border: none;
        background-color: #ff7701;
        font-size: 14px;
        font-weight: bold;
        cursor: pointer;
        color: #fff;
    }
    .triangle {
        position: absolute;
        right: 15px;
        top: 10px;
        margin: 0;
    }
}
