// Button
.btn{
	text-align: center;
	font-size: 12px;
	line-height: 16px;
	width: 100px;
	padding: 10px;
	border-radius: 3px;
	border: 1px solid #414141;
	background: #ffffff;
	color: #414141;
	cursor: pointer;
	white-space: nowrap;
}
button[disabled]{
	background: #b4b4b4;
	border: 1px solid #b4b4b4;
	cursor: no-drop;
}

/* Button size */
.btn-small{
	font-size: 12px;
	padding: 2px 8px;
}  
.btn-large{
	font-size: 18px;
	padding: 10px 8px;
}
/* Button color */  
.btn-primary{
	border: 1px solid $bg-primary;
	background: $bg-primary;
	color: #ffffff;
}
.btn-white{
	border: 1px solid #ffffff;
	background: #ffffff;
	color: $color-primary;
}
.btn-light{
	border: 1px solid #5a5a5a;
	background: #5a5a5a;
	color: #ffffff;
}
.btn-grey{
	border: 1px solid #9c9c9c;
	background: #9c9c9c;
	color: #ffffff;
}
/* Button border */
.btn-outline{
	background: #ffffff;
	&.btn-primary{
		color: $color-primary;
	}
	&.btn-light{
		color: #5a5a5a;
	}
}
/* Button other property */
.btn-fullwidth{
	width: 100%;
}
.btn-disabled{
	cursor: not-allowed;
	opacity: .5;
	filter: alpha(opacity=50); 
}
.btn-radius{
	border-radius: 40px 40px;
}
.btn-space{
	letter-spacing: 30px;
	text-indent: 30px;
}
.btn-area {
    margin: 15px 0;
    .btn:first-child{
    	margin-right: 10px;
    }
    &._one{
    	.btn{
    		margin-right: 0;
    	}
    }
}