/* 基本按钮 */

.btn-normal {
    font-size: .15rem;
    line-height: .16rem;
    padding: .06rem .12rem;
    text-align: center;
    border: 1px solid;
    cursor: pointer;
    white-space: nowrap;
    background-color: currentColor;
}

.btn-normal::first-line {
    color: #fff;
}


/* 变色按钮特殊处理 */

.btn-normal._white {
    /*box-shadow: inset 1px 0 #e3e4e6, inset 0 1px #e3e4e6, inset 0 -1px #e3e4e6, inset -1px 0 #e3e4e6;*/
}

.btn-normal._white::first-line {
    color: #33373d;
}

.btn-normal:active,
.btn-normal:hover {
    /*background-image: -webkit-linear-gradient(to top, rgba(0, 0, 0, .05), rgba(0, 0, 0, .05));*/
    /*background-image: linear-gradient(to top, rgba(0, 0, 0, .05), rgba(0, 0, 0, .05));*/
}


/* 线框实色的按钮 */

.btn-solid-line {
    font-size: .12rem;
    line-height: .16rem;
    padding: .06rem .12rem;
    text-align: center;
    border: 1px solid;
    cursor: pointer;
    white-space: nowrap;
    background-color: #ffffff;
}




/* 标记 */

.badge {
    display: inline-block;
    text-align: center;
    font-size: 12px;
    background: $color-primary;
    color: #ffffff;
    border-radius: 17px;
}

.badge-line {
    display: inline-block;
    min-width: .18rem;
    min-height: .18rem;
    line-height: 1.5;
    padding-left: .05rem;
    padding-right: .05rem;
    text-align: center;
    border-radius: .2rem;
    font-size: .12rem;
    border: 1px solid;
    background-color: transparent;
}


/* 文字提示框 */

.notice {
    font-size: .14rem;
    line-height: .16rem;
    padding: .1rem .1rem;
    text-align: center;
    border: 1px solid;
    color: #ffffff;
    line-height: 1.6;
    &._dark {
        background-color: #33373d;
    }
    &._gray {
        background-color: #9c9c9c;
    }
    &._blue {
        background-color: #4284ed;
    }
    &._green {
        background-color: #7ed321;
    }
    &._orange {
        background-color: #f0643a;
    }
    &._yellow {
        background-color: #f0c53a;
    }
    &._purple {
        background-color: #a091ff;
    }
    &._red {
        background-color: #e0533d;
    }
}
