
/*checkbox*/

.label-checkbox {
    padding: 0;
    width: 100%;
    margin: 5px 0;
    overflow: hidden;
    position: relative;
    span {
        display: inline-block;
        vertical-align: middle;
        width: 144px;
        padding-left: 15px;
        color: #333;
    }
}

.input-check {
    position: absolute;
    visibility: hidden;
    & + label {
        cursor: pointer;
        vertical-align: middle;
        display: inline-block;
        width: 20px;
        height: 20px;
        background: url('../images/icon/icon-checkbox.png') no-repeat 0 0;
    }
    &:checked + label {
        background-position: 0 -20px;
    }
    &._checkbox {
        & + label {
            background: url('../images/icon/icon-checkbox-cart.png') no-repeat 0 0;
        }
    }
    &._radio {
        & + label {
            background: url('../images/icon/icon-radio.png') no-repeat 0 0;
        }
    }
    &._checkbox,
    &._radio {
        &:checked + label {
            background-position: 0 -20px;
        }
    }
}


/*switch*/

.label-switch {
    display: inline-block;
    vertical-align: middle;
    position: relative;
    cursor: pointer;
    width: 70px;
    height: 30px;
    border-radius: 80px;
    .checkbox {
        width: 70px;
        height: 30px;
        line-height: 30px;
        border-radius: 80px;
        font-size: 12px;
        padding-left: 10px;
        color: #ffffff;
        background: #e5e5e5;
        border: none;
        cursor: pointer;
        position: relative;
        z-index: 0;
        transition-duration: 300ms;
        &:before {
            content: '个人';
            text-align: right;
            position: absolute;
            left: 0;
            top: 0;
            z-index: 1;
            background: #4cd964;
            color: #ffffff;
            width: 70px;
            height: 30px;
            padding-right: 10px;
            border-radius: 80px;
        }
        &:after {
            content: ' ';
            background: #fff;
            position: absolute;
            z-index: 2;
            top: 1px;
            left: 1px;
            transform: translateX(0px);
            transition-duration: 300ms;
            width: 28px;
            height: 28px;
            border-radius: 50%;
        }
    }
    input[type="checkbox"] {
        display: none;
        &:checked + .checkbox {
            background: #4cd964;
            &:before {
                transform: scale(0);
            }
            &:after {
                transform: translateX(40px);
            }
        }
    }
}