.float-box-bg {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 310px;
    // height: 130px;
    height: 105px;
    background: #3c3c3c;
    opacity: 0.9;
    filter: alpha(opacity=90);
    z-index: 10;
    transition: all 500ms linear 0s;
    -webkit-transition: all 500ms linear 0s;
    border-radius: 0 310px 310px 0;
    .icon-arrow-right-float{
        position: absolute;
        top: 54px;
        right: 45px;
        z-index: 10;
        cursor: pointer;
    }
}

.float-box {
    _position: absolute;
    position: fixed;
    bottom: 0;
    z-index: 100;
    left: 0;
    width: 310px;
    transition: all 500ms linear 0s;
    -webkit-transition: all 500ms linear 0s;
    // overflow: hidden;
    cursor: pointer;
    &.float-show {
        width: 100%;
        .float-pic{
            // left: -107px;
        }
        .float-box-bg {
            width: 100%;
            border-radius: 0;
        }
        .float-search {
            opacity: 1;
            visibility: visible;
        }
        .float-text {
            opacity: 1;
            visibility: visible;
        }
        .icon-arrow-right-float{
            visibility: hidden;
        }
        .icon-float-close{
            visibility: visible;
        }
    }
    .float-pic {
        // position: absolute;
        // left: 35px;
        z-index: 11;
        display: inline-block;
        margin-top: -63px;
        vertical-align: top;
    }
    .float-content {
        position: relative;
        // width: $w-content;
        width: 1220px;
        padding-left: 37px;
        // margin: 63px auto 0;
        margin: 0 auto 0;
        z-index: 120;
        // height: 130px;
        height: 105px;
        font-size: 0;
    }
    .float-text {
        display: inline-block;
        // padding-top: 45px;
        margin-left: 34px;
        margin-top: 27px;
        font-size: 24px;
        line-height: 53px;
        // letter-spacing: -3px;
        color: #ffffff;
        vertical-align: top;
        opacity: 0;
        visibility: hidden;
        transition: opacity 500ms linear 0s;
        .text-primary {
            font-size: 36px;
        }
    }
    .float-search {
        height: 53px;
        line-height: 53px;
        border: 0;
        position: absolute;
        right: 60px;
        // margin-top: 40px;
        margin-top: 27px;
        font-size: 0;
        opacity: 0;
        visibility: hidden;
        transition: opacity 500ms linear 0s;
        &:after{
            content: '';
            display: inline-block;
            width: 100%;
            border-bottom: 1px solid #6e6e6e;
            margin-top: 1px;
            position: absolute;
            bottom: -2px;
            left: 0;
            border-radius: 0px 0 100px 100px;
        }
        .text {
            // width: 163px;
            width: 203px;
            height: 53px;
            font-size: 14px;
            border: 1px solid #ffffff;
            border-right: 1px solid #c9c9c9;
            border-radius: 4px 0 0 4px;
        }
        .btn {
            height: 53px;
            font-size: 18px;
            transition: opacity 300ms linear 0s;
            -webkit-transition: opacity 300ms linear 0s;
        }
    }
    .icon-float-close{
        position: absolute;
        right: 0;
        top: 18px;
        width: 23px;
        height: 22px;
        cursor: pointer;
        visibility: hidden;
        transition: all .3s ease;
        &:hover{
          transform: rotate(180deg);
        }
    }
}




