
/*plusminus*/

.modified {
    float: left;
    overflow: hidden;
    background: #e0533d;
    border: 1px solid #000;
    border-radius: 0;
    height: 27px;
    .plus,
    .minus,
    .num {
        float: left;
        width: 27px;
        height: 27px;
        font-size: 16px;
        text-align: center;
        color: #000;
        background: #ffffff;
        cursor: pointer;
        &.disabled {
            cursor: not-allowed;
            color: #a0a0a0;
            background: #e5e5e5;
        }
        &._solid {
            color: #fff;
            background: #000;
        }
    }
    .num {
        width: 50px;
        border-left: 1px solid #000;
        border-right: 1px solid #000;
        border-radius: 0;
        overflow: hidden;
        cursor: normal;
        input {
            width: inherit;
            height: inherit;
            text-align: center;
            color: #000;
            border: none;
            background: transparent;
        }
    }
}

.num-txt {
    float: left;
    height: 27px;
    line-height: 27px;
    margin-left: 10px;
    color: $color-primary;
}
