//分页
.paging{
    width: 100%;
    text-align: center;
    margin-top: 40px;
    margin-bottom: 40px;
    color: #373737;
    font-size: 12px;
    a{
        color: #373737;
    }
    span{
        display: inline-block;
    }
    .item{
        margin-left: 3px;
        height: 25px;
        line-height: 24px;
        border-radius: 0;
    }
    ._disabled{
        color: #dcdcdc;
    }
    ._current{
        color: $color-primary;
    }
    .item-prev,
    .item-next{
        padding: 0 5px 0 5px;
        border: 1px solid $color-border;
    }
    .item-num{
        min-width: 25px;
        border: 1px solid $color-border;
        
    }
    .triangle{
        margin: 8px 10px 0 5px;
    }
}

