.input-form {
    width: 530px;
    .form-label {
        display: inline-block;
        margin: 5px 0;
    }
    .input-name {
        float: left;
        width: 110px;
        line-height: 47px;
        text-align: right;
    }
    .input-item {
        float: left;
        padding: 16px 5px 15px 5px;
        width: 410px;
        margin-left: 10px;
        border: 1px solid $color-border;
        outline: none;
        color: #000;
        &._short {
            width: 285px;
        }
        &._error {
            border-color: $color-primary;
        }
    }
    .tips-txt {
        margin-left: 120px;
        color: $color-primary;
        line-height: 20px;
    }
    .v-code {
        float: left;
        width: 124px;
        height: 46px;
        line-height: 46px;
        background: #f8f8f8;
        text-align: center;
        border: solid $color-border;
        border-width: 1px 1px 1px 0;
        display: inline-block;
        box-sizing: content-box;
    }
}

.form-input {
    display: block;
    margin-bottom: 18px;
    line-height: 30px;
    position: relative;
    input {
        font-size: 12px;
    }
    .unit {
        position: absolute;
        right: 3px;
        top: 1px;
        color: #221815;
        background: #ffffff;
        padding: 0 10px;
    }
    .input-text {
        position: relative;
        display: inline-block;
        width: 140px;
        height: 38px;
        line-height: 38px;
        border: 1px solid #ffffff;
        border-radius: 3px;
        color: #494848;
        padding-left: 7px;
        padding-right: 10px;
    }
    .form-button {
        margin-left: 76px;
        margin-top: 38px;
    }
    .btn {
        font-size: 16px;
    }
    div.error-tips {
        padding: 0 8px;
        white-space: nowrap;
        line-height: 38px;
        position: absolute;
        top: -55px;
        left: 0;
        background: #ffffff;
        border-radius: 3px;
        color: #666666;
        font-size: 12px;
        text-align: center;
        display: none !important;
        &.triangle {
            &._down {
                &:after {
                    left: 22px;
                    top: 38px;
                    border-width: 8px;
                    border-top-color: #ffffff;
                }
                &:before {
                    left: 22px;
                    top: 38px;
                    border-width: 9px;
                    border-top-color: #ffffff;
                }
            }
        }
    }
    &._error {
        &:hover {
            div.error-tips {
                display: block !important;
            }
        }
        .input-text {
            border-color: #a40000;
        }
    }
}
