//== set helpers.scss
//

/* 特殊约定 下划线「_***」的样式是不能单独使用的，它们都必须配合父亲，或者兄弟元素使用 */


/* 基础样式，放在最后 */

._fullwidth {
    width: 100%;
}

._space {
    letter-spacing: .3rem;
    text-indent: .3rem;
}

// nihao
._disabled {
    cursor: not-allowed;
    opacity: .5;
    filter: alpha(opacity=50);
}

._ellipsis {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

._ellipsis2 {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
}

._ellipsis3 {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
}


// Border 
.border-bottom {
    border-bottom: 1px solid $color-border;
}

.border-top {
    border-top: 1px solid $color-border;
}

// Float
.clearfix {
    @extend %clearfix;
}

.pull-left {
    @extend %float-left;
}

.pull-right {
    @extend %float-right;
}

// Text
.text-center {
    text-align: center !important;
}

.text-left {
    text-align: left !important;
}

.text-right {
    text-align: right !important;
}

// Show Modle
.hidden {
    visibility: hidden !important;
}

.show {
    visibility: visible !important;
}

.disI {
    display: inline !important;
}

.disB {
    display: block !important;
}

.disIB {
    display: inline-block !important;
}

.disN {
    display: none !important;
}

.posA {
    position: absolute !important;
}

.posR {
    position: relative !important;
}

.fixed {
    position: fixed !important;
}

// Padding
.pd-20 {
    padding: 20px;
}

//Size
.font12 {
    font-size: 12px;
}

.font14 {
    font-size: 14px;
}

.font16 {
    font-size: 16px;
}

.font18 {
    font-size: 18px;
}

//Background 
.bg-primary {
    background: $color-primary;
}

.ml35 {
    margin-left: 35px;
}

.lh2 {
    line-height: 2;
}


//table-width
.col-15 {
    width: 15%;
}

.col-30 {
    width: 30%;
}

.col-35 {
    width: 35%;
}

.col-40 {
    width: 40%;
}

.col-50 {
    width: 50%;
}

.col-65 {
    width: 65%;
}

//border
.border-line {
        border-bottom: 1px solid $color-border;
    }