/*定义弹层样式*/

.modal-overlay {
    position: fixed; // absolute -> fixed 变为全屏显示
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background: #b4b4b4;
    background: rgba(0, 0, 0, 0.5);
    z-index: 1000;
    visibility: hidden;
    &._in {
        visibility: visible;
    }
}

.modal-inner {
    position: absolute;
    top: 50%;
    margin-top: -120px;
    left: 50%;
    margin-left: -200px;
    padding: 10px 30px;
    background-color: #fff;
    z-index: 1001;
    text-align: center;
    line-height: 1.5;
    .modal-inner-title {
        position: relative;
        font-size: 16px;
        padding-bottom: 10px;
        border-bottom: 1px solid $color-border;
    }
    .modal-inner-content {
        font-size: 12px;
        color: #666;
        line-height: 2;
        margin: 20px 0;
    }
    .button {
        &:last-child {
            margin-left: 15px;
        }
    }
    .icon-close {
        position: absolute;
        right: 10px;
        top: 10px;
    }
}

.modal {
    width: 570px;
    min-height: 465px;
    font-size: 18px;
    color: #666666;
    position: fixed;
    left: 50%;
    top: 145px;
    margin-left: -285px;
    z-index: 1001;
    visibility: hidden;
    background: url('../images/bg-modal.jpg') repeat;
    transition: all .3s ease;
    opacity: 0;
    transform: translate(0, 0) scale(.8);
    overflow: hidden;
    &._in {
        visibility: visible;
        opacity: 1;
        transform: translate(0, 0) scale(1);
    }
    .btn-close {
        position: absolute;
        top: 30px;
        right: 26px;
        z-index: 1001;
        width: 33px;
        height: 33px;
        text-align: center;
        .icon-close {
            margin-top: 11px;
            width: 22px;
            height: 22px;
            background-size:100%;
        }
    }
}

.modal-tab {
    font-size: 0;
}

.modal-tab-menu {
    display: inline-block;
    vertical-align: top;
    .item {
        display: block;
        width: 60px;
        line-height: 2;
        padding: 53px 20px;
        background: #323232;
        color: #6f6f6f;
        font-size: 16px;
        &._active {
            background: $color-primary;
            color: #ffffff;
        }
    }
}

.modal-tab-content {
    display: inline-block;
    position: relative;
    width: 510px;
    padding-top: 80px;
    text-align: center;
    line-height: 1.7;
    color: #221815;
    display: none;
    &._show {
        display: inline-block;
    }
    &._order {
        .modal-form {
            margin-top: 45px;
        }
        .form-button {
            margin-top: 50px;
        }
    }
    .modal-text {
        line-height: 1.8;
        h1 {
            font-size: 36px;
            font-weight: normal;
        }
        h2 {
            font-size: 14px;
            font-weight: normal;
        }
        .num {
            font-size: 18px;
            padding: 0 4px;
        }
        .img-mascot {
            margin-top: 30px;
        }
        .text-tip {
            font-size: 12px;
            margin-top: 26px;
        }
    }
    .modal-form {
        width: 295px;
        text-align: left;
        margin: 20px auto;
        font-size: 14px;
        input {
            font-size: 14px;
        }
        .form-input {
            .input-text {
                width: 219px;
            }
            div.error-tips {
                left: 76px;
                z-index: 1002;
                border-bottom: 1px solid #656162;
                box-shadow: 1px 1px 4px #656162;
            }
        }
        .input-name {
            float: left;
            width: 76px;
            height: 38px;
            line-height: 38px;
            text-align: right;
            color: #221815;
        }
        .input-text {
            position: relative;
            display: inline-block;
            width: 219px;
            height: 38px;
            line-height: 38px;
            border: 1px solid #ffffff;
            border-radius: 3px;
            color: #494848;
            padding-left: 7px;
            padding-right: 10px;
        }
        .form-button {
            margin-left: 76px;
            margin-top: 38px;
        }
        .button {
            font-size: 16px;
        }
    }
}
