/* css for reset.scss */
@charset "utf-8";
body,
dl,
dd,
h1,
h2,
h3,
h4,
h5,
h6,
p,
form,
img{
	margin:0;
}
ol,
ul{
	margin:0;
	padding:0;
	list-style-type: none;
}
html {
  box-sizing: border-box;
}
body{
  font-family: "Microsoft YaHei", "微软雅黑",STHeiti, "Helvetica Neue", Helvetica, sans-serif;
}
html,body{
  width: 100%;
  height: 100%;
  background: #ffffff;
  font-size: 12px;
  min-width: 1280px;
}
*,
*:before,
*:after {
  box-sizing: border-box;
}
img {
  max-width: 100%;
  display: inline-block;
  box-sizing: content-box;
  text-align: center;
  position: relative;
  border: 0;
}
a {
  display: inline-block;
  margin: 0;
  padding: 0;
  text-decoration: none;
  outline: none;
  color: #333;
  /* 消除点击后出现闪动或灰色背景 */
  -webkit-tap-highlight-color: transparent;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
  table-layout: fixed;
}
em,
i{
	font-style: normal;
}
:focus{
	outline: none;
}
/* input/textarea*/
input[type="reset"]::-moz-focus-inner, input[type="button"]::-moz-focus-inner, input[type="submit"]::-moz-focus-inner, input[type="file"] > input[type="button"]::-moz-focus-inner { border:none; padding:0; }
select,
input,
textarea { vertical-align: middle; outline: none; resize:none; background: #fff; border:none;}
input[type="password"]:-webkit-autofill {
-webkit-box-shadow: 0 0 0 1000px white inset !important;

}

