/*分类标题*/

.content-block-title {
    padding: 15px;
    font-size: 15px;
    color: #414141;
}

h1.tit {
    display: inline-block;
    font-size: 16px;
    padding-bottom: 5px;
    margin: 30px 15px 10px 20px;
    color: #696969;
    border-bottom: 3px solid #888888;
}

.block {
    margin: 15px;
    padding: 5px;
}


/*swiper-pro*/

.main-img img {
    width: 100%;
    height: 100%;
}

.swiper-area01 {
    width: 640px;
    overflow: hidden;
    position: relative;
    .main-img {
        overflow: hidden;
        margin-right: 30px;
        width: 420px;
        height: 420px;
    }
}

.swiper-area02 {
    display: inline-block;
    width: 300px;
    height: 474px;
    border: 1px solid #e5e5e5;
    background: #fff;
    padding: 15px;
    box-sizing: border-box;
    margin-right: 21px;
    margin-bottom: 45px;
    position: relative;
    .main-img {
        width: 272px;
        height: 272px;
    }
    .goods-title {
        padding: 25px 10px;
        color: #666;
        font-size: 14px;
        line-height: 1.5;
        border-top: 1px solid #e5e5e5;
    }
    &:after {
        position: absolute;
        bottom: -12px;
        width: 270px;
        height: 12px;
        background: #f5f5f5;
    }
}

.swiper-area03 {
    width: 170px;
    background: #3c3939;
    padding-bottom: 5px;
    border: solid $color-border;
    border-width: 0 1px;
    box-sizing: border-box;
    .item-title {
        padding: 40px 0 15px 0;
        font-size: 16px;
        text-align: center;
        color: #fff;
    }
}



/*marquee*/

.marquee-bg {
    width: 100%;
    height: 50px;
    margin: 0 auto;
    position: relative;
    color: #fff;
    background-color: #000;
    .marquee {
        position: absolute;
        right: 100px;
        top: 10px;
        z-index: 12;
        width: 360px;
        .marquee-txt {
            width: 100%;
            text-decoration: none;
            color: #fff;
            display: inline-block;
            overflow: hidden;
            white-space: nowrap;
            font-size: 12px;
        }
        .icon-close {
            display: none;
        }
    }
}

/* leftSideBar */

