            }
        }
        &.opt7 {
            h2,
            h2:hover {
                background-position: 30px -218px;
            }
        }
        &.opt8 {
            h2,
            h2:hover {
                background-position: 30px -256px;
            }
        }
        &.opt9 {
            h2,
            h2:hover {
