/* cart */

.cart {
    position: relative;
    width: 145px;
    height: 30px;
    margin-left: 150px;
    margin-bottom: 200px;
    .cart-number {
        position: relative;
        z-index: 11;
        width: 120px;
        height: 100%;
        padding-left: 35px;
        background: #fffdf2 url(../images/icon/icon_cart.png) left 5px no-repeat;
        border: 1px solid #ffcc7b;
        h1 {
            width: auto;
            height: 30px;
            line-height: 30px;
            font: lighter 12px '\5b8b\4f53';
            span {
                font: bold 16px '\5fae\8f6f\96c5\9ed1';
                color: #f00;
            }
        }
    }
    .cart-pro {
        position: absolute;
        z-index: 10;
        width: 280px;
        height: auto;
        border: 1px solid #ffcc7b;
        top: 31px;
        right: -2px;
        background-color: #fffdf2;
        display: none;
    }
    .cart-have {
        margin: 15px 0 10px 10px;
        h2 {
            font-size: 12px;
        }
        .cart-proList {
            margin-top: 10px;
            height: 50px;
            width: 260px;
            img {
                float: left;
                width: 50px;
                height: 50px;
            }
            a {
                float: right;
                margin-top: 5px;
                color: #0069d4;
                &:hover {
                    color: #0069d4;
                }
            }
        }
        .cart-proList-detail {
            float: left;
            margin-top: 5px;
            h3 {
                font: lighter 12px '\5b8b\4f53';
                line-height: 18px;
                span {
                    color: #f00;
                }
                .cart-pro-state {
                    height: 18px;
                    overflow: hidden;
                }
                .valid {
                    width: 0px;
                    height: 0px;
                    font-size: 0;
                    margin-top: 0px;
                }
                .overdue {
                    width: 0px;
                    height: 0px;
                    font-size: 0;
                    margin-top: -18px;
                }
            }
            h4 {
                width: 85px;
                height: 18px;
                background-color: #e6e6e6;
                text-align: center;
                font-weight: lighter;
                line-height: 18px;
                color: #808080;
            }
        }
        .cut-dotted {
            width: 30px;
            border-bottom: 2px dotted #808080;
            margin: 10px 0px 0px 5px;
        }
        .cart-goto {
            margin: 15px auto;
            text-align: center;
            &:hover {
                color: #fff;
            }
        }
    }
    .cart-none {
        width: 270px;
        height: 57px;
        padding-left: 45px;
        background: url(../images/icon/icon_cart.png) 10px -25px no-repeat;
        p {
            font: lighter 12px '\5b8b\4f53';
            line-height: 57px;
            color: #808080;
        }
    }
}


/*table*/

.table-list {
    width: 100%;
    color: #666;
    text-align: center;
    line-height: 24px;
    font-size: 12px;
    border: solid $color-border;
    border-width: 0 0 1px 1px;
    tr {
        border: solid $color-border;
        border-width: 1px 0 0 0;
    }
    th {
        color: #000;
        background: #f8f8f8;
        border: solid $color-border;
        border-width: 0 1px 0 0;
    }
    td {
        padding: 5px;
        border: solid $color-border;
        border-width: 0 1px 0 0;
    }
}


/* 商品列表 */
.item-goods{
    overflow: hidden;
    color: #333333;
    background: #ffffff;
    float: left;
    .goods-info{
        padding: 0 20px;
    }
    .goods-title{
        font-size: 14px;
        font-weight: bold;
    }
    .goods-price{
        font-size: 12px;
        line-height: 1.5;
        em{
            font-size: 18px;
            color: #ff3300;
        }
        span{
            color: #666666;
            margin-left: 9px;
        }
    }
}
.list-default{
    width: 1213px;
    overflow: hidden;
    .item-goods{
        margin-right: 13px;
        margin-bottom: 12px;
        padding-bottom: 19px;
        width: 290px;
    }
    .goods-title{
        padding-top: 18px;
        padding-bottom: 18px;
    } 
}
